.link {
  display: -ms-flexbox;
  display: flex;
  height: 60px;
  line-height: 60px;
  font-size: 16px;
  color: #000;
  text-decoration: none;
  padding: 0 20px;
}
.link__title {
  -ms-flex-positive: 1;
      flex-grow: 1;
}
.link__extra {
  width: auto;
  color: #777;
}
.link__arrow {
  width: 16px;
  margin-left: 10px;
  background: #fff url('../../icons/arrow-r.svg') center / contain no-repeat;
}



