.menu {
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 48px;
  background-color: #fff;
  border-top: 1px solid #ddd;
}
.menu__item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-decoration: none;
}
.menu__item--icon {
  display: block;
  width: 22px;
  height: 22px;
  background-color: #ccc;
}
.menu__item--text {
  color: #666;
  font-size: 10px;
  margin-top: 4px;
}
.menu__item--movie {
  background: #fff url('../../icons/movie.png') 50% / contain no-repeat;
}
.menu__item--user {
  background: #fff url('../../icons/user.png') 50% / contain no-repeat;
}
.menu__item--active .menu__item--text {
  color: #ff4c64;
}
.menu__item--active .menu__item--movie {
  background: #fff url('../../icons/movie-solid.png') 50% / contain no-repeat;
}
.menu__item--active .menu__item--user {
  background: #fff url('../../icons/user-solid.png') 50% / contain no-repeat;
}



