.movie {
  text-align: left;
  display: -ms-flexbox;
  display: flex;
  border-bottom: 1px solid #ddd;
  padding: 10px 0px;
}
.movie__poster {
  width: 66px;
  height: 96px;
}
.movie__poster img {
  width: 100%;
  height: 100%;
}
.movie__detail {
  -ms-flex: 1 1;
      flex: 1 1;
  margin: 0 14px;
}
.movie__detail--name {
  color: black;
  font-size: 16px;
  font-weight: 400;
}
.movie__detail--score {
  color: #333;
  font-size: 10px;
  margin-top: 6px;
}
.movie__detail--score span {
  font-weight: bold;
  color: #ffa54b;
  margin-left: 4px;
}
.movie__detail--tag span {
  display: inline-block;
  font-size: 12px;
  padding: 2px 4px;
  line-height: 14px;
  height: 14px;
  border: 1px solid;
  border-radius: 4px;
  margin: 0px 2px;
}
.movie__detail--tag span.red {
  color: #ef6a79;
}
.movie__detail--tag span.blue {
  color: #5ba4e8;
}
.movie__detail--director,
.movie__detail--actor {
  color: #777;
  font-size: 10px;
}
.movie__detail--director {
  margin-top: 6px;
}
.movie__detail--actor {
  margin-top: 4px;
}
.movie__detail--tag {
  margin-top: 4px;
}
.movie__btn {
  display: -ms-flexbox;
  display: flex;
  width: 80px;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.movie__btn--done {
  width: 50px;
  height: 28px;
  line-height: 28px;
  border-radius: 14px;
  text-align: center;
  color: #fff;
  font-size: 12px;
  background-color: #ef6d83;
}
.movie__btn--price {
  color: #333;
  font-size: 12px;
  margin-top: 4px;
}



