.slide {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.slide img {
  width: 100%;
  height: 100%;
}
.slide .slick-dots {
  position: absolute;
  bottom: 0;
  margin-bottom: 8px;
}
.slide .slick-dots li {
  margin: 0px 4px;
}
.slide .slick-dots .slick-active button::before {
  display: none;
}
.slide .slick-dots .slick-active button::after {
  content: '';
  display: inline-block;
  width: 8px;
  height: 8px;
  background-color: #ffffff;
}
.slide .slick-dots button::before {
  display: none;
}
.slide .slick-dots button::after {
  content: '';
  display: inline-block;
  width: 8px;
  height: 8px;
  background-color: rgba(255, 255, 255, 0.6);
}



